import React from 'react';
import { Box } from '@mui/material';
import Layout from './Layout';
import { COLORS, BOX_SHADOW, BORDER_RADIUS } from './styles.js';
import Announcement from './components/AnnouncementHandler.js';

const App = () => {
    function handleOverlayClick(event) {
        if (event.target === event.currentTarget) {
            window.parent.postMessage({ type: 'outsideClick' }, '*');
        }
    }

    const overlayStyle = {
        position: 'fixed',
        top: '0',
        left: '0',
        height: '100vh',
        width: '100vw',
        zIndex: '0',
        backgroundColor: 'rgba(0, 0, 0, 0.5)'
    };

    const appStyle = {
        height: '66vh',
        width: '66vw',
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        zIndex: '1'
    };

    return (
        <div onClick={handleOverlayClick} style={overlayStyle}>
            <div style={appStyle}>
                <Box
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                        height: '100%',
                        width: '100%',
                        bgcolor: '#F0EFF4',
                        color: '#1F364D',
                        borderRadius: BORDER_RADIUS.main,
                        // boxShadow: BOX_SHADOW.mainBackground,
                        backgroundColor: COLORS.mainBackground,
                        padding: 2,
                        boxSizing: 'border-box',
                    }}
                >
                    <Layout />
                </Box>

                <Announcement
                    id="announce-001"
                    titles={[
                        "Dcouvrez la toute nouvelle version !",
                        "Partagez votre exprience et vos suggestions !",
                        "Contribuez  la mise  jour des donnes !"
                    ]}
                    messages={[
                        <p>
                            Il y a un an, nous avons lanc la premire version de notre extension pour l'intranet d'Epitech.<br />
                            Aujourd'hui, nous sommes ravis de vous prsenter une mise  jour majeure : notre extension s'ouvre dsormais directement dans une popup, offrant un accs plus rapide  vos statistiques.<br />
                            L'interface a t repense pour une meilleure exprience utilisateur, avec l'ajout de nouvelles donnes pour enrichir votre vue d'ensemble de l'anne scolaire, et plus encore  venir.
                        </p>,
                       <p>
                       Cette version est encore en cours d'amlioration, donc des bugs pourraient subsister.<br />
                       Vos retours sont essentiels pour nous aider  les rsoudre et  amliorer l'extension.<br />
                       Utilisez les <a href="https://github.com/RedBoardDev/EpitechIntranetStatistics/issues" target="_blank" rel="noopener noreferrer"> issues GitHub</a> pour partager vos suggestions et signaler les problmes rencontrs.
                   </p>,
                        <p>
                            Vous pouvez aussi contribuer  maintenir les donnes de l'extension  jour.<br />
                            Certaines informations ne sont pas rcuprables depuis l'intranet d'Epitech et doivent tre actualises dans le fichier
                            <a href="https://github.com/RedBoardDev/EpitechIntranetStatistics/blob/main/public/scripts/data/epitech_data.js" target="_blank" rel="noopener noreferrer"> epitech_data.js</a>.<br />
                            Signalez toute erreur via les <a href="https://github.com/RedBoardDev/EpitechIntranetStatistics/issues" target="_blank" rel="noopener noreferrer"> issues GitHub</a> pour garantir la prcision des donnes fournies.
                        </p>
                    ]}
                />
            </div>
        </div>

    );
};

export default App;
